* {font-family: 'Poppins', sans-serif;}

body {
   background-color: #1B6B93;
}

.rowContainer {
    display: flex;
    justify-content: center;
    align-items: center;
}

.containerEvent {
    background-color: #1B6B93;
    max-width: 800px;
    width: 80%;
    margin: auto;
    margin-top: 30px;
    margin-bottom: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.divider {
    background-color: #1B6B93;
    padding: 5%;
}

.titlesection {
    background-color: #a2ff86;
    text-align: center;
    padding-top: 5%;
    padding-bottom: 5%;
}

.title {
    width: 100%;
    margin-right: 0% !important;
    margin-left: 0% !important;
}

.titlesection h1 {
    font-weight: bolder;
    font-size: 138.5%;
}
.box {
    background-color: #a2ff86;
    border: 3px solid black;
    border-radius: 10px;
    display: flex;
}

#eventdetails {
    background-color: #1B6B93;
    padding: 5%;
    margin: 5% 2.5% 5% 5%;
    color: white;
    text-align: justify;
    align-content: center;
    align-self: center;
    max-width: fit-content;
}

#eventfoto {
    background-color: #1B6B93;
    padding: 5%;
    margin: 5% 2.5% 5% 2.5%;
    align-content: center;
    align-self: center;
    max-width: fit-content;
}



#eventdescription {
    background-color: #1B6B93;
    padding: 5%;
    margin: 5% 5% 5% 2.5%;
    color: white;
    text-align: justify;
    align-content: center;
    align-self: center;
    max-width: fit-content;
}

.calltoaction {
    text-align: center;
    display: flex;
    align-items: center;
}

#calltoaction {
    background-color: #a2ff86;
    font-weight: bold;
    padding: 10px 20px 10px 20px;
    margin: 0 auto; /*margen izq y derecho igual a 0 en ppio y el navegador lo centra,
    solo fuciona con block*/
    display: block; /*elementos block ocupan todo el ancho disponible y se muestran
    en "su propia línea"*/
}

@media screen and (min-width: 992px) {
    .divider {
        padding: 2%; 
    }
    .titlesection {
        padding-top: 2%;
        padding-bottom: 2%;
    }
    .box {
        border: 3px solid black;
        padding-top: 2%;
        padding-bottom: 2%;
        padding-right: 2%;
        padding-left: 2%;
    }

    #eventfoto {
        padding: 10px
    }
    #eventfoto img {
        width: 90em;
    }
}

@media screen and (max-width: 991px) {
    .box {
        display: flex;
        flex-direction: column;
        padding: 0;
    }
    #eventdetails {
        margin: 5% 2.5% 5% 5%;
        max-width: 100%;
        flex: 1 !important;
    }
    #eventdescription {
        margin: 2.5% 5% 5% 5%;
        max-width: fit-content;
    }
}